.ml-layer {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
}

.ml-layer .ml-modal {
    width: 520px;
    background: white;
    border-radius: 3px;
    position: absolute;
    top: 120px;
    left: 50%;
    margin-left: -260px;
    box-sizing: border-box;
}

.ml-layer .ml-modal header {
    line-height: 50px;
    padding: 0 20px;
    border-bottom: 1px solid #eee;
    font-size: 12px;
}

.ml-layer .ml-modal header>div {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.ml-layer .ml-modal header>div>div:first-child {
    float: left;
}

.ml-layer .ml-modal header>div>div:last-child {
    float: right;
    cursor: pointer;
}

.ml-layer .ml-modal footer {
    line-height: 50px;
    padding: 0 20px;
    border-top: 1px solid #eee;
    font-size: 12px;
    height: 50px;
    overflow: hidden;
}

.ml-layer .ml-modal footer>.ml-button {
    float: right;
}

.ml-layer .ml-modal main {
    box-sizing: border-box;
    padding: 20px;
    font-size: 14px;
}

.ml-button {
    display: inline-block;
    cursor: pointer;
    margin: 0 15px;
}

.ml-button span {
    display: inline-block;
    height: 30px;
    box-sizing: border-box;
    line-height: 28px;
    font-size: 12px;
    border-radius: 2px;
    padding: 0 15px;
    border: 1px solid transparent;
}

.ml-button span.default {
    border-color: #ccc;
}

.ml-button span.primary {
    color: white;
    background: blue;
    border-color: blue;
}

.ml-button span.danger {
    color: white;
    background: red;
    border-color: red;
}

.ml-button span.info {
    color: white;
    background: green;
    border-color: green;
}